import { Component, OnInit } from '@angular/core';
import { Subject }    from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';

import {
   debounceTime, distinctUntilChanged, switchMap
 } from 'rxjs/operators';

import {Item} from './../item';
import {ItemService} from './../item.service';

@Component({
  selector: 'app-busca-item',
  templateUrl: './busca-item.component.html',
  styleUrls: ['./busca-item.component.css']
})
export class BuscaItemComponent implements OnInit {

  itens$: Observable<Item[]>;
  private searchTerms = new Subject<string>();
  
  constructor( private itemService:ItemService ) { }

  search(term: string): void {
    this.searchTerms.next(term);
  }

  ngOnInit() {
       
       this.itens$ = this.searchTerms.pipe(
      // wait 300ms after each keystroke before considering the term
      debounceTime(300),
 
      // ignore new term if same as previous term
      distinctUntilChanged(),
 
      // switch to new search observable each time the term changes
      switchMap((term: string) => this.itemService.buscaItens(term)),
    ); 

  }

  

}


