import { Component } from '@angular/core';
declare var $:any;
import * as jQuery from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';

  ngAfterViewInit() {
       $(".button-collapse").sideNav();
    }

    hideBar(){
    	 $('.button-collapse').sideNav('hide');
    }
}





